* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
  font-family: "Open Sans", sans-serif;
}
body {
  overflow: hidden;
}
html {
  /* background: rgb(186, 80, 63); */
  overflow: hidden;
  height: 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background: -moz-linear-gradient(
    0deg,
    rgba(186, 80, 63, 1) 0%,
    rgba(227, 97, 77, 1) 100%
  );
  background: -webkit-linear-gradient(
    0deg,
    rgba(186, 80, 63, 1) 0%,
    rgba(227, 97, 77, 1) 100%
  );
  background: linear-gradient(
    0deg,
    rgba(186, 80, 63, 1) 0%,
    rgba(227, 97, 77, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#ba503f",endColorstr="#e3614d",GradientType=1);
}
