.noSelect {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */
  -khtml-user-select: none; /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}
#loadingicon {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
#hideloading {
  width: 100%;
  height: 100%;
  position: fixed;
  display: flex;
  justify-content: space-around;
  align-items: center;
  animation: hideload 4s ease;
  opacity: 0;
  z-index: -100;
}
@keyframes hideload {
  0% {
    opacity: 1;
  }
}
#home {
  height: 100%;
  position: absolute;
  width: 100%;
  overflow: hidden;
}
#hiddengen {
  display: none;
}
#bg {
  width: 100%;
  height: 100%;
  position: fixed;
  align-items: center;
  display: flex;
  justify-content: space-between;
}
#bg img {
  min-height: 110%;
  opacity: 0.15;
}
#popupcon {
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.837);
  z-index: 10;
  animation: fadepopup 1s ease-in;
  opacity: 1;
  flex-wrap: wrap;
  justify-content: space-around;
}
#forinfo {
  font-size: 12px;
  position: absolute;
  color: #fffbd4;
  margin-left: auto;
  margin-right: auto;
  margin-top: 20px;
  animation: fadefromtop 1.5s ease-in;
  opacity: 0.5;
  letter-spacing: 5px;
}
@keyframes fadefromtop {
  0% {
    opacity: 0;
    margin-top: -30px;
  }
  70% {
    opacity: 0;
  }
}
.hide {
  display: none;
}
.show {
  display: flex;
}
@keyframes fadepopup {
  0% {
    opacity: 0;
  }
}

#popup {
  width: 300px;
  height: 170px;
  position: fixed;
  background-color: transparent;
  display: flex;
  justify-content: space-around;
  align-items: center;
  font-size: 50px;
  color: #fffbd4;
  font-family: "Stalemate", cursive;
  left: 50%;
  top: 50%;
  box-shadow: 0 0 0.8em #e3614d74;
  border-radius: 20px;
  transform: translate(-50%, -50%);
}
#rlogoleft {
  -ms-transform: rotate(180deg); /* IE 9 */
  transform: rotate(180deg);
  margin-left: -300px;
  margin-top: 100px;
  animation: fade 2s ease-out;
}
#rlogoright {
  margin-right: -300px;
  margin-top: -100px;
  animation: fade2 2s ease-out;
}

@keyframes fade {
  0% {
    opacity: 0%;
    margin-left: -600px;
  }
  50% {
    opacity: 0%;
    margin-left: -600px;
  }
}
@keyframes fade2 {
  0% {
    opacity: 0%;
    margin-right: -600px;
  }
  50% {
    opacity: 0%;
    margin-left: -600px;
  }
}

#main_body {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
}
#comingsoon {
  width: 100%;
  display: flex;
  justify-content: center;
  letter-spacing: 8px;
  color: #fffbd4;
  margin-top: 40px;
  opacity: 0.68;
  font-size: 14px;
  font-weight: 100;
  animation: slidin 2.5s ease-out;
}
@keyframes slidin {
  0% {
    opacity: 0;
    margin-top: -40px;
  }
  80% {
    opacity: 0;
  }
}

#middle-section {
  width: 100%;
  display: flex;
  animation: fadein 3s ease-out;
}
@keyframes fadein {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
}
.left {
  width: 100%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.right {
  width: 100%;
  display: flex;
  align-items: center;
  animation: righta 2s ease-in;
}
.noright {
  animation: fadeout 1s linear;
  transform: translate3d(100px, 0px, 0px);
  opacity: 0;
  width: 0%;
}

#smalogocon {
  margin-right: 30px;
}
#polyshape {
  width: 450px;
  height: 450px;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  animation: spin 8s ease-out 2s;
  animation-iteration-count: infinite;
}
.thankyou {
  position: relative;
  text-align: center;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  25% {
    transform: rotate(2deg);
  }
  50% {
    transform: rotate(0deg);
  }
  75% {
    transform: rotate(-2deg);
  }
  100% {
    transform: rotate(0deg);
  }
}

#polyshape img {
  height: 140px;
}

#jointhewaitcon {
  margin-left: 30px;
}
#title,
#title2 {
  font-family: "Abel", sans-serif;
  font-weight: 100;
  font-size: 75px;
  color: #fffbd4;
  margin-top: -55px;
}
#title2 {
  margin-top: -15px;
}

#emailcon {
  display: flex;
  margin-top: 20px;
  align-items: center;
}
#emailcon input {
  padding: 12px 15px;
  background-color: transparent;
  border: 1px solid hsla(54, 100%, 92%, 0.562);
  border-radius: 12px;
  font-size: 13px;
  width: 300px;
  color: #fffbd4;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

#emailcon input::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgba(255, 251, 214, 0.409);
  opacity: 1; /* Firefox */
}

#emailcon input:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: rrgba(255, 251, 214, 0.409);
}

#emailcon input::-ms-input-placeholder {
  /* Microsoft Edge */
  color: rgba(255, 251, 214, 0.409);
}

#emailcondiv {
  background-color: #fffbd4;
  font-size: 13px;
  color: #e3614d;
  margin-left: 10px;
  padding: 0px 20px;
  height: 45px;
  display: flex;
  align-items: center;
  border-radius: 12px;
  cursor: pointer;
  min-width: fit-content;
  transition: width 1s;
}
.reduce {
  min-width: 100px;
}

#poweredbytx {
  width: 100%;
  display: flex;
  justify-content: center;
  letter-spacing: 8px;
  color: #fffbd4;
  margin-bottom: 40px;
  opacity: 0.68;
  align-self: flex-end;
  font-weight: 100;
  animation: slidin2 3s ease-in;
  font-size: 14px;
}
p {
  font-family: "Abel", sans-serif;
}
@keyframes slidin2 {
  0% {
    opacity: 0;
    margin-bottom: -40px;
  }
  87% {
    opacity: 0;
  }
}
#m-comingsoon {
  position: absolute;
  left: 50px;
  top: 50px;
  font-size: 17px;
  letter-spacing: 8px;
  color: #e3614d;
}

#mobile-logo {
  background-color: #fffbd4;
  width: 90%;
  display: flex;
  position: relative;
  justify-content: space-around;
  min-height: 600px;
  border-bottom-right-radius: 90%;
  display: none;
  margin-bottom: 100px;
  animation: loadin 2s ease-out;
  clip-path: circle(100% at 50% 50%);
  box-shadow: 0 0 1.5em rgba(0, 0, 0, 0.515);
}
#validate {
  font-size: 13px;
  margin-top: 10px;
  color: rgba(255, 184, 173, 0.781);
  animation: validation 1s ease;
  position: absolute;
  margin-top: 80px;
}
#displaynone {
  display: none;
}

@keyframes validation {
  0% {
    opacity: 0;
  }
}

@keyframes loadin {
  0% {
    opacity: 0;
    clip-path: circle(0% at -100% -100%);
  }
  50% {
    opacity: 0;
    clip-path: circle(0% at -100% -100%);
  }
}

#mobile-logo img {
  width: 50%;
  margin-right: 20%;
  margin-top: 40px;
}

@media only screen and (max-width: 1050px) {
  #polyshape {
    width: 400px;
    height: 400px;
  }
  #title,
  #title2 {
    font-size: 65px;
  }
  #emailcon input {
    padding: 10px 15px;
    width: 250px;
  }
  #emailcondiv {
    height: 42px;
  }
  #jointhewaitcon {
    margin-left: 15px;
  }
  #smalogocon {
    margin-right: 15px;
  }
}

@media only screen and (max-width: 840px) {
  #polyshape {
    width: 300px;
    height: 300px;
  }
  #polyshape img {
    width: 120px;
  }
  #title,
  #title2 {
    font-size: 55px;
  }
  #emailcon input {
    width: 200px;
    font-size: 12px;
  }
  #emailcondiv {
    font-size: 12px;
    height: 41px;
  }
  #jointhewaitcon {
    margin-left: 10px;
  }
  #smalogocon {
    margin-right: 10px;
  }
  #poweredbytx {
    font-size: 12px;
  }
  #comingsoon {
    font-size: 13px;
  }
  #validate {
    font-size: 12px;
  }
}

@media only screen and (max-width: 700px) {
  #mobile-logo {
    display: flex;
  }
  #bg {
    justify-content: flex-end;
  }
  #main_body {
    min-height: 100%;
    overflow: auto;
  }
  #polyshape {
    width: 500px;
    height: 500px;
  }
  #middle-section {
    flex-wrap: wrap;
  }
  #rlogoleft {
    display: none;
  }
  #comingsoon {
    display: none;
  }
  .right {
    justify-content: space-evenly;
    margin-bottom: 90px;
  }
  .left {
    justify-content: space-evenly;
    display: none;
  }
  #polyshape img {
    width: 180px;
  }
  #title,
  #title2 {
    font-size: 55px;
  }
  #emailcon {
    width: 100%;
  }
  #jointhewaitcon {
    width: 100%;
    padding: 0px 10%;
  }
  #emailcon input {
    width: 70%;
    font-size: 12px;
  }
  #emailcondiv {
    font-size: 12px;
  }
  #jointhewaitcon {
    margin-left: 10px;
  }
  #smalogocon {
    margin-right: 10px;
  }
}
@media only screen and (max-width: 530px) {
  #mobile-logo {
    min-height: 400px;
  }
  #jointhewaitcon {
    padding: 0px 6%;
  }
  #validate {
    margin-top: 70px;
  }
}
@media only screen and (max-width: 420px) {
  #popup {
    width: 250px;
    font-size: 40px;
  }
}

@media only screen and (max-width: 365px) {
  #mobile-logo {
    min-height: 350px;
  }
  #title,
  #title2 {
    font-size: 40px;
  }
  #title2 {
    margin-top: -5px;
  }
  #poweredbytx {
    font-size: 11px;
    letter-spacing: 5px;
  }
  #m-comingsoon {
    font-size: 10px;
    letter-spacing: 5px;
    top: 40px;
    left: 40px;
  }
  .right {
    margin-bottom: 50px;
  }
}
@media only screen and (max-width: 308px) {
  #popup {
    width: 200px;
  }
}
@media only screen and (max-width: 280px) {
  #mobile-logo {
    min-height: 300px;
  }
}
